import React, { Fragment } from 'react';
import DataTable, { EditableOper } from 'components/DataTable';
import Icon from 'components/Icon';
import { Button } from 'antd';
const Tip = DataTable.Tip;

export const columns1 = [
  {
    title: '',
    name: 'name',
    tableItem: {}
  },
  {
    title: '',
    name: 'age',
    tableItem: {}
  },
  {
    title: '',
    name: 'address',
    tableItem: {}
  },
  {
    title: '',
    tableItem: {
      width: 180,
      render: (text, record) => (
        <DataTable.Oper>
          <Button tooltip="">
            <Icon type="edit" />
          </Button>
          <Button tooltip="">
            <Icon type="trash" />
          </Button>
        </DataTable.Oper>
      )
    }
  }
];

export const columns2 = [
  {
    title: '',
    name: 'name',
    tableItem: {}
  },
  {
    title: '',
    name: 'age',
    tableItem: {}
  },
  {
    title: '',
    name: 'address',
    tableItem: {
      width: 200,
      render: text => <Tip>{text + text + text + text + text + text}</Tip>
    }
  }
];

export const columns3 = [
  {
    title: '',
    name: 'role',
    dict: [
      { code: '1', codeName: '' },
      { code: '2', codeName: '' },
      { code: '3', codeName: '' }
    ],
    tableItem: {}
  }
];

export const columns4 = [
  {
    title: '',
    name: 'name',
    tableItem: {}
  },
  {
    title: '',
    name: 'age',
    tableItem: {
      sorter: true
    }
  },
  {
    title: '',
    name: 'address',
    tableItem: {}
  }
];

export const columns5 = (self, editingKey) => [
  {
    title: '',
    name: 'name',
    tableItem: {
      type: 'input',
      editing: (text, record) => record.id === editingKey,
      rules: [{ required: true, message: '' }]
    }
  },
  {
    title: '',
    name: 'age',
    tableItem: {
      type: 'number',
      editing: (text, record) => record.id === editingKey
    }
  },
  {
    title: '',
    name: 'role',
    dict: [
      { code: '1', codeName: '' },
      { code: '2', codeName: '' },
      { code: '3', codeName: '' }
    ],
    tableItem: {
      type: 'select',
      editing: (text, record) => record.id === editingKey
    }
  },
  {
    title: '',
    tableItem: {
      width: 180,
      render: (text, record) => (
        <EditableOper>
          {form =>
            record.id === editingKey ? (
              <Fragment>
                <a onClick={e => self.onSave(record, form)}></a>
                <a onClick={e => self.onCancelEdit()}></a>
              </Fragment>
            ) : (
              <a onClick={e => self.onEdit(record)}></a>
            )
          }
        </EditableOper>
      )
    }
  }
];
